.portfolio__about-content {
  display: flex;
  gap: 6rem;
}

.portfolio__about-content_image {
  flex: 0.7;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.portfolio__about-content_image img {
  width: 100%;
  max-width: 450px;
  border-radius: 5px;
}

.portfolio__about-content_text {
  flex: 1;
}

.portfolio__about-content_text h3,
.portfolio__about-content_text p {
  font-family: var(--font-family);
}

.portfolio__about-content_text h3 {
  margin-bottom: 1rem;
  font-size: 20px;
  display: inline-block;
}

.portfolio__about-content_text p {
  text-align: justify;
  font-size: 16px;
  line-height: 25px;
  color: var(--color-text);
}

@media screen and (max-width: 900px) {
  .portfolio__about-content {
    flex-direction: column;
  }

  .portfolio__about-content_image {
    justify-content: center;
    align-items: center;
  }
}

@media screen and (max-width: 600px) {
  .portfolio__about-content_text h3 {
    font-size: 18px;
  }
  
  .portfolio__about-content_text p {
    font-size: 14px;
    line-height: 22px;
  }
}