.portfolio__contact-content {
  display: flex;
  gap: 2.5rem;
}

.portfolio__contact-content_details,
.portfolio__contact-content_form {
  flex: 1;
}

.portfolio__contact-content_details h3,
.portfolio__contact-content_form h3 {
  font-family: var(--font-family);
  margin-bottom: 1rem;
  display: inline-block;
}

.portfolio__contact-content_details > p {
  font-family: var(--font-family);
  margin-bottom: 1rem;
  color: var(--color-text);
}

.portfolio__contact-content_details-container {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.portfolio__contact-content_details-info {
  display: flex;
  align-items: center;
}

.portfolio__contact-content_details-info svg {
  font-size: 1.5rem;
  margin-right: 1rem;
  color: #fff;
}

.portfolio__contact-content_details-info h5 {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 0.2rem;
  color: #fff;
}

.portfolio__contact-content_details-info p {
  font-family: var(--font-family);
  font-size: 15px;
  font-weight: 400;
  color: var(--color-text);
}

.portfolio__contact-content_form form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.portfolio__contact-content_form input,
.portfolio__contact-content_form textarea {
  width: 100%;
  padding: 0.4rem 0.6rem;
  font-family: var(--font-family);
  font-size: 16px;
  outline: none;
  border: 2px solid var(--color-footer);
  background: var(--color-footer);
  color: #fff;
  border-radius: 5px;
}

.portfolio__contact-content_form button {
  padding: 0.5rem;
  font-family: var(--font-family);
  font-size: 17px;
  font-weight: 600;
  background: rgb(255, 60, 0);
  color: #fff;
  border: none;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.portfolio__contact-content_form button:hover {
  cursor: pointer;
  background: rgb(224, 55, 4);
}

.portfolio__contact-content_form button span {
  display: flex;
  align-items: center;
  justify-content: center;
}

.portfolio__contact-content_form button span svg {
  margin-left: 0.5rem;
}

@media screen and (max-width: 900px) {
  .portfolio__contact-content {
    flex-direction: column;
  }

  .portfolio__contact-content_details-info h5 {
    font-size: 15px;
  }

  .portfolio__contact-content_details-info p {
    font-size: 14px;
  }

  .portfolio__contact-content_details-info svg {
    font-size: 1.3rem;
  }

  .portfolio__contact-content_form input,
  .portfolio__contact-content_form textarea {
    font-size: 14px;
  }

  .portfolio__contact-content_form button {
    font-size: 15px;
  }
}

@media screen and (max-width: 600px) {
  .portfolio__contact-content_details > p {
    font-size: 14px;
  }
}