.portfolio__header {
  min-height: 70vh;
}

.portfolio__header-content {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.portfolio__header-content-text {
  flex: 1;
}

.portfolio__header-content-image {
  flex: 0.7;
  display: flex;
  align-items: center;
  justify-content: center;
}

.portfolio__header-content-image div {
  width: 100%;
  max-width: 450px;
}

.portfolio__header-content-image img {
  width: 100%;
}

.portfolio__header-content-text p {
  font-family: var(--font-family);
  color: var(--color-subtext);
  font-size: 20px;
  margin-bottom: 0.5rem;
}

.portfolio__header-content-text h1 {
  font-family: var(--font-family);
  font-size: 66px;
  font-weight: 800;
  letter-spacing: -0.04em;
  margin-bottom: 0.5rem;
}

.portfolio__header-content-text h3 {
  font-family: var(--font-family);
  color: var(--color-text);
  font-size: 24px;
  margin-bottom: 2rem;
}

.portfolio__header-content-text a {
  font-family: var(--font-family);
  padding: 0.5rem 1rem;
  background: rgb(255, 60, 0);
  color: #ffffff;
  font-weight: 600;
  font-size: 20px;
  border-radius: 5px;
}

@media screen and (max-width: 1200px) {
  .portfolio__header-content-text h1 {
    font-size: 62px;
  }
}

@media screen and (max-width: 1050px) {
  .portfolio__header {
    padding-top: 8rem;
  }

  .portfolio__header-content {
    flex-direction: column;
    align-items: unset;
  }
}

@media screen and (max-width: 750px) {
  .portfolio__header-content-text h1 {
    font-size: 55px;
  }

  .portfolio__header-content-text p {
    font-size: 16px;
  }

  .portfolio__header-content-text h3 {
    font-size: 20px;
  }
}

@media screen and (max-width: 600px) {
  .portfolio__header-content-text h1 {
    font-size: 45px;
  }
}
