.portfolio__projects-content_tabs {
  text-align: center;
  margin: 2rem;
}

.portfolio__projects-content_tabs button {
  border: none;
  outline: none;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 500;
  color: var(--color-text);
  padding: 0.3rem 1rem;
  cursor: pointer;
  background: none;
  border-bottom: 3px solid rgba(255, 60, 0, 0);
}

.portfolio__projects-content_tabs .active {
  border-bottom: 3px solid rgb(255, 60, 0);
  font-weight: 600;
}

@media screen and (max-width: 900px) {
  .portfolio__projects-content_tabs button {
    font-size: 16px;
  }
}

@media screen and (max-width: 600px) {
  .portfolio__projects-content_tabs button {
    font-size: 14px;
  }
}