.portfolio__navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 6rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}

.portfolio__navbar-logo h1 {
  font-family: var(--font-family);
  color: #fff;
  font-weight: 800;
  font-size: 1.6rem;
}

.portfolio__navbar-links {
  display: flex;
}

.portfolio__navbar-links p,
.portfolio__navbar-menu_container p {
  margin-left: 1.5rem;
  font-family: var(--font-family);
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  text-transform: capitalize;
  transition: all 0.3s ease;
}

.portfolio__navbar-links p:hover,
.portfolio__navbar-menu_container p:hover {
  color: var(--color-text);
}

.portfolio__navbar-menu {
  display: none;
  position: relative;
}

.portfolio__navbar-menu svg {
  cursor: pointer;
  color: #fff;
}

.portfolio__navbar-menu_container {
  position: absolute;
  top: 40px;
  right: 0;
  padding: 1rem 2rem;
  min-width: 160px;
  background: var(--color-footer);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  text-align: end;
  box-shadow: 0 0 4px rgba(0,0,0,0.2);
}

.portfolio__navbar-menu_container p {
  margin: 1rem 0;
}

@media screen and (max-width: 1050px) {
  .portfolio__navbar-links {
    display: none;
  }

  .portfolio__navbar-menu {
    display: flex;
  }
}

@media screen and (max-width: 900px) {
  .portfolio__navbar {
    padding: 1.2rem 4rem;
  }

    .portfolio__navbar-links {
    display: none;
  }

  .portfolio__navbar-menu {
    display: flex;
  }
}

@media screen and (max-width: 600px) {
  .portfolio__navbar {
    padding: 1rem 2rem;
  }
}