.portfolio__footer {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  background: var(--color-footer);
}

.portfolio__footer p {
  text-align: center;
  color: #fff;
  font-size: 16px;
  font-family: var(--font-family);
}

@media screen and (max-width: 900px) {
  .portfolio__footer p {
    font-size: 14px;
  }
}

@media screen and (max-width: 600px) {
  .portfolio__footer p {
    font-size: 12px;
  }
}