.carousel {
  overflow: hidden;
}

.inner {
  white-space: nowrap;
}

.carousel-item {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.indicators {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
}

.indicators button {
  width: 10px;
  height: 10px;
  margin: 5px;
  border-radius: 50%;
  border: none;
  background-color: var(--color-text);
  opacity: 0.4;
}

.indicators button.active {
  background-color: rgb(255, 60, 0);
  color: #fff;
  opacity: 1;
}