.portfolio__projects-content_web {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

@media screen and (max-width: 1050px) {
  .portfolio__projects-content_web {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 600px) {
  .portfolio__projects-content_web {
    grid-template-columns: repeat(1, 1fr);
  }
}