.portfolio__projects-content_graphics {
  display: grid;
  /* grid-template-columns: repeat(4, 1fr); */
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.portfolio__projects-content_graphics-img {
  width: 100%;
}

.portfolio__projects-content_graphics-img img {
  width: 100%;
  transition: all 0.3s ease;
}

.portfolio__projects-content_graphics-img:hover img {
  transform: scale(1.05);
  opacity: 0.6;
  cursor: pointer;
}